export default {
  name: 'myFormAuditType',
  props: {
    itemIndex: {
      type: [Number, String],
      default: 0
    },
    customUserStatus: {
      type: Boolean,
      default: false
    }
  },
  computed: {

  },
  data() {
    return {
      popVisible: false,
      value: ''
    }
  },
  methods: {
    addPop(){
      this.popVisible = true;
    },
    selAuditType(event, type, extra){
      this.popVisible = false;
      this.$emit("click", type, extra, parseInt(this.itemIndex));
    },
    showPop(){
      this.$emit("showPop");
    }
  }
}
